import { fragen } from "./fragen";
import { awQuiz } from "./awQuiz";
import { assiQuiz } from "./assiQuiz";
import "ol/ol.css";

import { computedFrom } from "aurelia-framework";

interface Antwort {
  text: string;
  richtig: boolean;
  antwort: boolean | undefined;
}
interface Frage {
  Nr: string;
  Kategorie: string;
  Frage: string;
  Antworten: Antwort[];
}
export class App {
  public message = "Hello Worldi!";
  public questions: Array<Frage> = fragen as Frage[];
  public awQuizQuestions: Array<Frage> = awQuiz as Frage[];
  public assiQuizQuestions: Array<Frage> = assiQuiz as Frage[];
  public wrongAnswers: Array<Frage> = [];
  public frage = this.questions.at(0);
  public showSolution = false;
  public categories = [];
  public assiCategories = [];
  public selectedCategory = null;
  private position = 0;
  public searchTerm = "";
  public checkedAnswers: Antwort[] = [];
  public correct = 0;
  public quizMode: string = "dd";

  constructor() {
    this.questions = fragen as Frage[];
    this.categories = this.calculateCategories();
    this.assiCategories = this.calculateAssiCategories();
  }

  public next(): void {
    if (this.isAnswerCorrect()) {
      this.showSolution = false;
      this.checkedAnswers = [];

      if (this.position < this.questions.length) {
        this.position++;
      }

      this.frage = this.questions.at(this.position);
      this.frage.Antworten = this.shuffleArray(
        this.frage.Antworten,
        this.frage.Antworten.length
      ) as Antwort[];
    } else {
      this.showSolution = true;
    }
  }
  @computedFrom("quizMode")
  public get mode(): string {
    return this.quizMode;
  }
  @computedFrom("position")
  public get gameOver(): boolean {
    return this.position === this.questions.length;
  }

  public search(): any {
    this.questions = this.questions.filter((q) => {
      if (q.Frage) {
        return q.Frage.toLowerCase().includes(this.searchTerm.toLowerCase());
      }
    });
    this.position = 0;
    this.frage = this.questions[0];
  }

  private isAnswerCorrect() {
    let isCorrect = true;
    if (this.checkedAnswers.length === 0) {
      if (this.wrongAnswers.length === 0) {
        this.wrongAnswers.push({ ...this.frage });
      } else if (
        this.wrongAnswers.filter((wrong) =>
          wrong.Frage.includes(this.frage.Frage)
        ).length === 0
      ) {
        this.wrongAnswers.push({ ...this.frage });
      }

      isCorrect = false;
    }
    for (let index = 0; index < this.checkedAnswers.length; index++) {
      const userAnswer = this.checkedAnswers[index];
      for (let index2 = 0; index2 < this.frage.Antworten.length; index2++) {
        const correctAnswer = this.frage.Antworten[index2];
        if (userAnswer.text === correctAnswer.text && !correctAnswer.richtig) {
          if (this.wrongAnswers.length === 0) {
            this.wrongAnswers.push({ ...this.frage });
          } else if (
            this.wrongAnswers.filter(
              (wrong) => wrong.Frage === this.frage.Frage
            ).length === 0
          ) {
            console.log("frage not found");
            this.wrongAnswers.push({ ...this.frage });
          }
          isCorrect = false;
        }
      }
    }
    // this.correct = this.correctAnswers();
    return isCorrect;
  }
  @computedFrom("position")
  public get correctAnswers(): number {
    if (this.position === 0) {
      return 0;
    }
    const amount = 100 - (this.wrongAnswers.length / (this.position + 1)) * 100;
    return Math.floor(amount);
  }

  @computedFrom("position")
  public get questionNo(): number {
    if (this.position === 0) {
      return 1;
    } else if (this.position === this.questions.length) {
      return this.position - 1;
    } else {
      return this.position;
    }
  }

  public calculateCategories(): Array<any> {
    const unique = [
      ...Array.from(new Set(this.questions.map((item) => item.Kategorie))),
    ];
    console.log("unique questions");
    console.log(unique);
    return unique;
  }
  public calculateAssiCategories(): Array<any> {
    const unique = [
      ...Array.from(
        new Set(this.assiQuizQuestions.map((item) => item.Kategorie))
      ),
    ];
    console.log("unique questions");
    console.log(unique);
    return unique;
  }
  public filterQuestions(cat: string) {
    console.log("filterQuestions called: " + this.selectedCategory);
    this.showSolution = false;
    this.position = 0;
    this.selectedCategory = cat;
    if (this.selectedCategory === "null") {
      console.log("selected categorie is null");
      this.questions = fragen as Frage[];
      this.frage = this.questions[0];
      return;
    }
    if (cat === "wrong") {
      console.log("selected categorie is wrong answers");
      console.log(this.wrongAnswers);
      this.questions = this.wrongAnswers;
      this.frage = this.questions[0];
      return;
    }
    this.questions = fragen as Frage[];
    this.questions = this.questions.filter(
      (q) => q.Kategorie === this.selectedCategory
    );
    this.frage = this.questions[0];
  }

  public filterAssiQuestions(cat: string) {
    console.log("filterQuestions called: " + this.selectedCategory);
    this.showSolution = false;
    this.position = 0;
    this.selectedCategory = cat;
    if (this.selectedCategory === "null") {
      console.log("selected categorie is null");
      this.questions = this.assiQuizQuestions as Frage[];
      this.frage = this.questions[0];
      return;
    }
    if (cat === "wrong") {
      console.log("selected categorie is wrong answers");
      console.log(this.wrongAnswers);
      this.questions = this.wrongAnswers;
      this.frage = this.questions[0];
      return;
    }
    this.questions = this.assiQuizQuestions as Frage[];
    this.questions = this.questions.filter(
      (q) => q.Kategorie === this.selectedCategory
    );
    this.frage = this.questions[0];
  }
  public shuffle() {
    this.showSolution = false;
    this.shuffleArray(this.questions, this.questions.length);
  }

  public awQuiz() {
    this.quizMode = "anwärter";
    this.questions = this.shuffleArray(
      this.awQuizQuestions as Frage[],
      60
    ) as Frage[];
    this.frage = this.questions[0];
  }
  public assiQuiz() {
    this.quizMode = "assistenten";
    const filtered = this.assiQuizQuestions.filter((frage) => {
      return frage.Kategorie !== "Lernverhalten/Ausbildung";
    });
    this.questions = this.shuffleArray(
      filtered as Frage[],
      filtered.length - 1
    ) as Frage[];
    this.frage = this.questions[0];
  }
  public pruefung() {
    this.quizMode = "pruefung";
    const filtered = this.assiQuizQuestions.filter((frage) => {
      return frage.Frage.includes("--p");
    });
    this.questions = this.shuffleArray(
      filtered as Frage[],
      filtered.length - 1
    ) as Frage[];
    this.frage = this.questions[0];
  }
  private shuffleArray(
    array: Frage[] | Antwort[],
    lenght: number
  ): Frage[] | Antwort[] {
    this.showSolution = false;

    console.log("shuffle all: ");
    let currentIndex = array.length,
      randomIndex;

    // While there remain elements to shuffle...
    while (currentIndex != 0) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      // And swap it with the current element.
      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex],
        array[currentIndex],
      ];
    }
    return array.slice(0, lenght);
  }

  public solution(): void {
    this.showSolution = true;
  }

  public reset(): void {
    this.showSolution = false;
    this.questions = fragen as Frage[];
    this.frage = this.questions.at(0);
    this.wrongAnswers = [];
    this.searchTerm = "";
    this.position = 0;
    this.selectedCategory = null;
  }
}
